import { useState } from "react";

const useError = () => {
    const [message, setMessage] = useState('');
    //const error = new Error;
    const findError = (status) => {
        if (status < 400 && status >= 300)
            setMessage(' .')
        else if (status >= 500)
            setMessage('  .  ,    .')
        else switch (status) {
            case 400: setMessage(' 400: Bad Request. \n .      .'); break;
            case 403: setMessage(' 403: Forbidden \n    .'); break;
            case 404: setMessage(' 404: Not Found \n   .'); break;
            case 413: setMessage(' 413: Request Entity Too Large \n  ,     10MB.'); break;
            case 429: setMessage(' 429: Too many requests \n  ,   .'); break;
            default: setMessage('');
        }
    }
    return [message, findError];
}

export default useError;